* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body,
#root {
  height: 100%;
}

html {
  color-scheme: dark;
}

body {
  background: #000000;
  color: #ffffff;
  --border-radius: 6px;
  --spacing: 8px;
  --blue: #276df1;
  --dark-blue: #245eca;
}

h1 {
  font-weight: 400;
}

.spacer {
  flex-grow: 0;
  flex-shrink: 1;
}

.start-over {
  position: block;
  padding: var(--spacing);
  left: var(--spacing);
  bottom: var(--spacing);
  background: #000000;
  border-radius: var(--border-radius);
  border: 1px solid transparent;
  outline: none;
  cursor: pointer;
  transition: background-color 0.2s;
  color: #c7c7c7;
}

.start-over-placeholder {
  display: none;
  visibility: hidden;
  padding: var(--spacing);
}

/* #region Intro Prompt */
.intro-prompt-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.intro-prompt {
  padding: calc(2 * var(--spacing));
  text-align: center;
}

.intro-prompt-button {
  background: none;
  border: none;
  color: #ffffff;
  padding: var(--spacing);
  border-radius: var(--border-radius);
  border: 1px solid transparent;
  outline: none;
  cursor: pointer;
  transition: background-color 0.2s;
}
/* #endregion */

/* #region Question */
.background-canvas {
  border: 0;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}

.questions-wrapper {
  height: 100%;
  width: 100%;
  padding: calc(2* var(--spacing));
}

.question-wrapper-x {
  display: flex;
  flex-direction: row;
}

.question-wrapper-y {
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
}

.question {
  display: flex;
  flex-direction: column;
  gap: calc(2 * var(--spacing));
  padding: calc(2 * var(--spacing));
  max-width: 100%;
  backdrop-filter: blur(8px);
  border-radius: var(--border-radius);
  background: rgba(0, 0, 0, 0.308);
}

.question-send {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: var(--spacing);
  background: var(--blue);
  border-radius: var(--border-radius);
  border: 1px solid var(--blue);
  outline: none;
  cursor: pointer;
  transition: background-color 0.2s;
}

.question-img {
  border-radius: var(--border-radius);
  min-width: 0;
  min-height: 0;
  max-width: 100%;
  user-select: none;
}

.question-response {
  display: flex;
  flex-direction: row;
  gap: calc(2 * var(--spacing));
}

.question-textarea-wrapper {
  flex: 1;
}

.question-textarea {
  flex: 1;
  max-width: 100%;
  min-height: 0;
  border: 1px solid #303030;
  resize: none;
  background: #000000bb;
  color: #ffffff;
  padding: var(--spacing);
  border-radius: var(--border-radius);
  overflow: hidden;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1rem;
}

.question-send svg {
  height: 1rem;
  width: 1rem;
  fill: #ffffff;
}

/* #endregion */

/* #region Response */
.response-wrapper {
  flex: 1;
  display: flex;
  flex-direction: row;
}

.response-title {
  flex-grow: 0;
  flex-shrink: 1;
  flex-basis: 375px;
  padding: calc(2 * var(--spacing));
  font-size: 4rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.responses {
  flex: 1;
  display: flex;
  flex-direction: column;
  padding: calc(2 * var(--spacing));
  overflow-y: auto;
  gap: calc(2 * var(--spacing));
}

.response {
  max-width: 500px;
  display: flex;
  flex-direction: column;
  gap: calc(var(--spacing));
}
.response-group {
  display: flex;
  flex-direction: column;
  gap: var(--spacing);
}

.response-img {
  border-radius: var(--border-radius);
  min-width: 0;
  min-height: 0;
  max-width: 100%;
  user-select: none;
}

.response-wrapper-x {
  flex: 1;
  display: flex;
  flex-direction: row;
}

.response-text-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}

.response-text {
  padding: var(--spacing);
  border-radius: var(--border-radius) 0 var(--border-radius)
    var(--border-radius);
}

.response-user {
  color: #c7c7c7;
  font-size: 0.875rem;
  text-align: right;
}

.answer-text {
  background: linear-gradient(to left, #3e3e49, #2d2d35);
  padding: var(--spacing);
  border-radius: 0 var(--border-radius) var(--border-radius)
    var(--border-radius);
}

.answer-text-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}

.answer-user {
  color: #c7c7c7;
  font-size: 0.875rem;
  text-align: left;
}
/* #endregion */

/* #region States */
.question-send:hover:not(:disabled),
.intro-prompt-button:hover:not(:disabled),
.start-over:hover:not(:disabled) {
  background: var(--dark-blue);
}

.question-send:focus-visible,
.question-textarea:focus-visible,
.intro-prompt-button:focus-visible,
.start-over:focus-visible {
  box-shadow: 0 0 0 4px rgb(39 94 254 / 30%);
  border: solid 1px var(--blue);
  outline: none;
}

.question-send:disabled,
.intro-prompt-button:disabled,
.start-over:disabled {
  background: #4c84eb;
  cursor: not-allowed;
}
/* #endregion */

/* #region Animations */
@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fade-out {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
/* #endregion */

/* #region Media Queries */
@media only screen and (max-width: 768px) {
  .response-wrapper {
    flex-direction: column;
    align-items: center;
  }

  .response-wrapper-x {
    justify-content: center;
  }

  .response {
    width: 100%;
  }

  .response-title {
    font-size: 2rem;
    flex-basis: 0;
  }

  .start-over {
    position: fixed;
    width: 100%;
    left: 0;
    bottom: 0;
    background: var(--blue);
    border-radius: 0;
  }

  .start-over-placeholder {
    display: block;
  }

  .spacer {
    display: none;
  }

  .questions-wrapper {
    padding: var(--spacing);
  }
}
/* #endregion */
